<!---------------    -------------------->
<template>
  <div class="container-fluid" >
    <div  class="itemlist-top" >
      <!---------------    -------------------->
      <b-row class="mb-3">
        <b-col>
          <b-button-group  class="itemlist-button-group">
            <b-button @click="onSort(1)" variant="transparent"></b-button>
            <b-button  @click="onSort(2)" variant="transparent"></b-button>
            <b-dropdown variant="transparent" right text="">
              <b-dropdown-item @click="onSort(3)" ></b-dropdown-item>
              <b-dropdown-item @click="onSort(4)"></b-dropdown-item>
            </b-dropdown>
            <b-dropdown variant="transparent" right text="">
              <b-dropdown-item  @click="onSort(5)"></b-dropdown-item>
              <b-dropdown-item  @click="onSort(6)"></b-dropdown-item>
            </b-dropdown>
          </b-button-group>
        </b-col>
        <b-col>
          <b-input-group
            class="itemlist-input-group"
            @change="onSort(7)"
            size="sm"
          >
            <b-form-input v-model="searchString"></b-form-input>
            <b-input-group-append>
              <b-button @click="onSort(7)" size="sm" style="background-color:transparent;color:black;">
                  <b-icon :icon="'search'"></b-icon>
                </b-button>
            </b-input-group-append>
          </b-input-group>
        </b-col>
      </b-row>
    </div>
    <div  class="itemlist-content">
      <!---------------    -------------------->
      <b-row>
        <b-card-group id="itemlist" class="col-md-3"  v-for="items in filterlist[currentPage-1]" :key="items.productno" deck>


            <b-card
              id="card"
              no-body
              bg-variant="transparent"
              class="border-0"
              >
              <router-link :to="{name:'item',params:{id:items.productno}}">
                <b-card-img :src="items.imagesrc" img-alt="" img-top></b-card-img>
              </router-link>
                <b-card-body>
                  <p>{{items.producttitle}}
                  <a class="card-text">${{items.productprice}}</a></p>
                  <b-button class="CartAddButton" v-on:click="CartClick(items)" variant="danger">
                    
                  </b-button>
                </b-card-body>
            </b-card>
        </b-card-group>
      </b-row>
    </div>
    <b-pagination
      v-model="currentPage"
      :total-rows="rows"
      :per-page="perPage"
      aria-controls="card"
      class="pageController"
    ></b-pagination>
  </div>
</template>

<script>
export default {
  data(){
      return{
        // ---routere------
        items:[],
         //-- pagination 
        currentPage:'1',// 
        perPage: 8,     // Card
        // (rows  / perPage  ) = 

        //--itemlist 
        sortMethod:'', // itemlist  => 
        searchString:''
      }
    },watch:{
      $route(now){
        if(this.$route.params.str=='login'){
            this.$root.$emit('bv::show::modal', 'modal')
          }
      }
    } ,mounted() {
        this.$http.get(process.env.VUE_APP_API+'/Product/GetProduct')
        .then(response => {
            this.items = response.data;
        });
    },computed:{
      ProductPanel(){
        return this.ProductPanelVisible
      },
      DescriptionPanel(){
        return !this.ProductPanelVisible
      },
      rows(){ // 
        var sum=0
        this.filterlist.forEach((value, index) => {
          sum =sum + value.length
        });
        return sum
      },
      /*
      itemNo:99,itemName:'
      itemNo:2,itemName:'
      itemNo:3,itemName:'
      itemNo:4,itemName:'
      itemNo:5,itemName:'
      itemNo:6,itemName:'
      itemNo:7,itemName:'
      itemNo:66,itemName:'
      itemNo:88,itemName:'
      * */

      filterlist(){ // LIST
        if(this.$store.state.filterString=='99'){   //
          return this.initList(this.initSort(this.items))
        }else if(this.$store.state.filterString=='66'){
          var filterlist = this.items.filter(item => {  //
            return item.OnSale===1
          })
          return this.initList(this.initSort(filterlist))
        }else if(this.$store.state.filterString=='88'){
            //
            console.log('')
        }else{
          var filterlist = this.items.filter(item => { //
            return item.CategoryNo=== this.$store.state.filterString
          })
          return this.initList(this.initSort(filterlist)) //  
        }
      }
    },methods:{
      CartClick(items){ //
        if(this.$store.state.items.some(item => item.productno === items.productno)){
            var i = this.$store.state.items.findIndex(x => x.productno === items.productno);
            this.$store.commit("cartDelete",i)
        }else{
          this.$store.commit({
              type:"cartInsert",
              productno: items.productno,
              producttitle:items.producttitle,
              productspecification:'',
              productmeasurement:'',
              productprice:items.productprice
          })
        }
      },
       //--  items ------
      initList(oldList){
        var newList = []
        var templist=[] // PUSH  LIST   
        var cnt=7 //   cnt + 1 
        oldList.forEach((value, index) => {
            templist.push(value)
            if((index%cnt==0&index!=0)|oldList.length==1|index==oldList.length-1){
              newList.push(templist)
              templist=[]
            }
        });
        return newList
      },
      // itemlist 
      onSort(s){
        this.sortMethod = s
        console.log(`(${s})`)
      },
      initSort(oldList){
        var key
        if(this.sortMethod==1){        //
          return oldList.filter(item => {
            key = 'Sold'
            return oldList.sort(function(a,b){
              var x = parseInt(a[key]);
              var y = parseInt(b[key]);
              return x<y?1:-1
            })
          })
        }else if(this.sortMethod==2){  //
          return oldList.filter(item => {
            return item.Recommend===1
          })
        }else if(this.sortMethod==3){  //
          key = 'productprice'
          return oldList.sort(function(a,b){
            var x = parseInt(a[key]);
            var y = parseInt(b[key]);
            return x>y?1:-1
          })
        }else if(this.sortMethod==4){ //
          key = 'productprice'
          return oldList.sort(function(a,b){
            var x = parseInt(a[key]);
            var y = parseInt(b[key]);
            return x<y?1:-1
          })
        }else if(this.sortMethod==5){  //
          key = 'OnDate'
          return oldList.sort(function(a, b) {
            var x = new Date(b[key])
            var y = new Date(a[key])
            return x>y?1:-1
          })
        }else if(this.sortMethod==6){ //
          key = 'OnDate'
          return oldList.sort(function(a, b) {
            var x = new Date(b[key])
            var y = new Date(a[key])
            return x<y?1:-1
          })
        }else if(this.sortMethod==7){
          return oldList.filter(
            item => {return (item.producttitle.toLowerCase().indexOf(this.searchString.toLowerCase()) > -1) | (item.ProductSubTitle.indexOf(this.searchString) > -1)
          })
        }else{
          this.sortMethod=''
          return oldList
        }
      }
    }
}
</script>
<style>
.itemlist-top{
  width: 100%;
  margin: auto;
  margin-top: 2vh;
}
.itemlist-button-group{
  width: 50vh;
  margin:auto;
}
.itemlist-input-group{
  width: 30vh;
  margin: auto;
  margin-right: 3vh;
}
.ButtonBackGroundTransparent{
  background-color: transparent;
}
.itemlist-content{
  width:95%;
  margin: auto;
}
#itemlist{
  margin: auto;
  margin-top: 3vh;
}
.card-text{
  color: red;
}
.CartAddButton{
  height: 5vh;
  width: 100%;
}
.pageController{
  width:30vh;
  margin:auto;
  margin-top: 3vh;
  margin-bottom: 3vh;
}
</style>



