<template>
  <div id="app">
    <div class="header">
      <b-navbar  variant="light" class="header-navbar">
        <b-navbar-brand>
          <b-button v-b-toggle.sidebar variant="Info" size="lg">
            <b-icon icon="list"></b-icon>
          </b-button>
          <router-link to="/" ></router-link>
          </b-navbar-brand>
        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
        <b-collapse id="nav-collapse" is-nav>
          <!-- Right aligned nav items -->
          <b-navbar-nav class="ml-auto">
            <b-nav-item id="CartPopover" @click="Cart" right>
              <b-icon icon="Cart4" aria-hidden="true"></b-icon>
              
              <b-badge variant="danger">{{itemsCount}}</b-badge>
            </b-nav-item>
            <b-popover
              target="CartPopover"
              placement="bottomleft"
              triggers="hover focus"
              custom-class="popoverCart"
            >
              <template #title><b-icon icon="Cart4" aria-hidden="true"></b-icon></template>
              <b-table  :items="items" :fields="fields"  >
                <template #cell(productprice)="row">
                  <p style="color:red;">${{row.item.productprice}}</p>
                </template>
                <template #cell(actions)="row">
                  <a v-on:click="cancelCart(row.index)">
                    <b-icon icon="x-circle-fill" ></b-icon>
                  </a>
                </template>
              </b-table>
            </b-popover>
            <b-nav-item-dropdown  right>
              <!-- Using 'button-content' slot -->
              <template #button-content>
                <b-icon icon="person-fill"></b-icon>
              </template>
              <b-dropdown-item v-if="LoginStatus"> <router-link to="/member" ></router-link></b-dropdown-item>
              <b-dropdown-item  @click="Login">{{LoginStatusText}}</b-dropdown-item>
            </b-nav-item-dropdown>
            <b-modal id="modal" ref="modal-center" size="md" hide-footer centered title="/">
              <account></account>
            </b-modal>
          </b-navbar-nav>
        </b-collapse>
      </b-navbar>
    </div>
    <div class="content">
      <!---------------    -------------------->
      <router-view></router-view>
    </div>
    <div class="footer">
        <center><p>Author:Marcus<br>Email:god850405@gmail.com</p></center>
    </div>
    <b-sidebar  id="sidebar" aria-labelledby="sidebartitle" no-header no-header-close shadow>
      <!---------------    -------------------->
        <template #default="{ hide }">
              <div class="d-flex bg-dark text-light align-items-center px-3 py-2">
                  <b-button variant="transparent" @click="hide" style="width:100vh">
                    <b-icon icon="arrow-left-circle-fill" variant="light"></b-icon>
                    <span style="color:#eee;"></span>
                  </b-button>
              </div>
              <sidebar></sidebar>
        </template>

    </b-sidebar>
  </div>
</template>

<script>
import account from "./components/account.vue";
import sidebar from "./components/sidebar.vue";
import * as types from '@/types'
export default {
  name: "App",
  data(){
    return{
        fields: [
          {key: 'productno', label: '',class: 'field-center'},
          { key: 'producttitle', label: ''},
          { key: 'productspecification', label: '',class:'field-center'},
          { key: 'productmeasurement', label: '',class:'field-center'},
          { key: 'productquantity', label: '',class: 'field-center'},
          { key: 'productprice',label: '', class: 'field-center'},
          { key: 'actions', label: '' ,class:'field-center'}
        ]
    }
  },components:{
    account,
    sidebar
  }
  ,computed:{
    items(){
      return this.$store.state.items
    },
    itemsCount(){
      return this.$store.state.itemsCount
    },
    LoginStatusText(){
      return this.$store.state.token ? '' : '/'
    }
    ,
    LoginStatus(){
      return !!this.$store.state.token
    }
  },
  methods:{
    Cart(){
      this.$router.push({
          name:'cart'
      }).catch(err => {console.log(err)})
    },
    Login(){
      if(this.$store.state.token!=null){
          this.$store.commit(types.LOGOUT)
          this.$store.commit("resetUser")
          this.$root.$bvToast.toast(
            '',
            {
              title: '',
              autoHideDelay: 2000,
              appendToast: true
            }
          )
          this.$router.push({
              name:'itemlist'
          }).catch(err => {console.log(err)})
      }else{
          this.$refs['modal-center'].toggle('#toggle-btn')
      }
    },
    cancelCart(index){
      //  row.index
      console.log(` (${index}) ` );
      this.$store.commit("cartDelete",index)
    }
  }
}
</script>

<style>
*{
    margin: 0;
    padding: 0;
    list-style: none;
    font-family: arial,san-serf;
}
html,body{
    background-color: #eee;
    margin: 0;
    height: 100%;
}
#app {
    height:inherit; /**/
    /*flex--headercenterfooter*/
    display: flex;
    flex-direction: column;
}
.content{
    flex: 1; /*flex--centernavmain*/
    display: flex; /*flex--navmain*/
    background-color: #DAE6EC;
}
.footer{
    height: 50px; /*flex--footer*/
    background-color: #3490C5;
    font-weight:bold;
    color: rgb(53, 53, 53);
}

.popoverCart{
  min-width: 300px;
}
.field-price{
  color:red;
}
.field-center{
  text-align: center;
}

</style>



